@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  background-color: #232833;
}

.App {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  font-family: 'Montserrat';
}
.mainPage {
  margin: 0;
  width: 90%;
  height: 80vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.portrait {
  width: 30%;
  display: flex;
  justify-content: center;
  margin-right: 20px;
}

.portrait>img {
  width: 330px;
  height: 500px;
  padding: 20px;
  border: .2px solid #ff5d5538;
}

.portrait>img:hover {
  border: .2px solid white;
}



.startingPanel {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.startingPanel>p:nth-child(-n + 2) {
  color: #5b7186;
  font-size: 120px;
  letter-spacing: 20px;
  font-weight: bold;
  margin: 0;
}

.startingPanel>p:nth-child(2) {
  margin-bottom: 40px;
} 

.startingPanel>p:nth-child(4) {
  color: #ff5d555b;
  letter-spacing: 1px;
  font-size: 20px;
  font-weight: lighter;
}

.startingPanel>h1 {
  color: #ff5d55ce;
  font-size: 36px;
  letter-spacing: 15px;
}

.icons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.socialIcon {
  margin: 30px 8px 0;
  color: rgb(8, 68, 164);
  font-size: 17px;
}

.socialIcon:hover {
  opacity: 0.5;
}



/*****************************************/

@media (max-width: 1440px) {
  .mainPage {
    height: 60vh;
  }

  .portrait>img {
    width: 280px;
    height: 425px;
  }

  .startingPanel {
    margin-left: 20px;
  }

  .startingPanel>p:nth-child(-n + 2) {
    font-size: 80px;
    letter-spacing: 10px;
  }

  .startingPanel>p:nth-child(4) {
    letter-spacing: 1px;
    font-size: 18px;
  }
  
  .startingPanel>h1 {
    font-size: 30px;
    letter-spacing: 3px;
  }
}

/* **************************************** */
@media (max-width: 1024px) {
  .portrait>img {
    width: 230px;
    height: 350px;
  }

  .startingPanel>p:nth-child(-n + 2) {
    font-size: 60px;
    letter-spacing: 10px;
  }

  .startingPanel>p:nth-child(4) {
    letter-spacing: 0;
    font-size: 15px;
  }
  
  .startingPanel>h1 {
    font-size: 21px;
    letter-spacing: 3px;
  }
}

/* **************************************** */

@media (max-width: 767px) {
  .portrait>img {
    margin: 0 25px 0 0;
    width: 180px;
    height: 275px;
  }

  .startingPanel>p:nth-child(-n + 2) {
    font-size: 30px;
    letter-spacing: 10px;
  }

  .startingPanel>p:nth-child(4) {
    letter-spacing: 1px;
    font-size: 10px;
  }
  
  .startingPanel>h1 {
    font-size: 15px;
    letter-spacing: 10px;
  }
}
  

/* **************************************** */

@media (max-width: 520px) {
  .portrait>img {
    display: none;
  }
}






.navigation {
  margin: 0;
  width: 100%;
  height: 100vh;
}

.navigationList {
  margin: 0;
  padding: 25px 0 20px;
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  height: 10vh;
}

.listItem>a {
  font-size: 27px;
  color: #6e869c;
  text-decoration: none;
}

.listItem>a:hover, .active {
  color: #0844a4;
  font-weight: bold;
} 

.active {
  letter-spacing: 3px;
}


.about-me {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 90%;
  margin: 0 auto;
  height: 80vh;
  box-sizing: border-box;
}

.about-me__left {
  width: 60%;
  height: auto;
  margin-right: 10px;
}

.about-me__right {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: row;
  padding: 25px;

}

.about-me__right span {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.skillbox {
  width: 40%;
  height: auto;
  text-align: center;
}

.skillbox-skillIcon {
  font-size: 120px;
  color: #ff5d55a9;
}

.skillstransition-appear {
  opacity: 0.1;
  -webkit-transform: translateX(80px);
          transform: translateX(80px);
}

.skillstransition-appear.skillstransition-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: all 1.3s ease;
}

.skillstexttransition-appear {
  opacity: 0.1;
  -webkit-transform: translateX(-60px);
          transform: translateX(-60px);
}

.skillstexttransition-appear.skillstexttransition-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: all 1.3s ease;
}

.about-me__text {
  padding: 25px;
  color: #a09090f8;
  margin: 0;
  font-size: 17.3px;
  letter-spacing: 0;
  text-align: justify;

}

.about-me__text_transition-appear {
  opacity: 0;
}

.about-me__text_transition-appear.about-me__text_transition-appear-active {
  opacity: 1;
  transition: 2.5s ease-in-out;
}

.about-me__text-header {
  font-size: 50px;
  color: white;
}


/*****************************************/
@media (max-width: 1630px) {
  .aboutLeft>p {
    font-size: 22px;
  }
}

@media (max-width: 1440px) {
  .aboutLeft>p {
    font-size: 20px;
  }
}


/* **************************************** */
@media (max-width: 1164px) {
  .aboutLeft>p {
    font-size: 18px;
  }
}

/* **************************************** */
@media (max-width: 1024px) {
  .aboutLeft>p {
    font-size: 17px;
  }
}

