    width: 100%;
    display: flex;
    flex-direction: row;
    flex-flow: row wrap;
    align-items: center;
}

.gallery__item1 {
    background-image: url(/static/media/cinema.dde59e14.jpeg);
}
.gallery__item2 {
    background-image: url(/static/media/baking.c264cea4.jpg);
}
.gallery__item3 {
    background-image: url(/static/media/music.7e5c41bf.jpeg);
}
.gallery__item4 {
    background-image: url(/static/media/videogames.ba79c612.jpg);
}
.gallery__item5 {
    background-image: url(/static/media/boardgames.4674507f.jpeg);
}
.gallery__item6 {
    background-image: url(/static/media/ceramic.830e8aff.jpg);
}

div[class^="gallery__item"] {
    width: 40%;
    height: 31.2%;
    margin: 9px;
    transition: .5s ease-in-out;
    background-size: 100%  100%;
    background-repeat: no-repeat;
    background-position: center;
    border: 2px solid white;
}

div[class^="gallery__item"]:hover {
    background-size: 130% 130%;
}

.gallery>div:nth-child(-n + 2) {
    margin-top: 0;
}
  
.gallery>div:nth-child(n + 5) {
    margin-bottom: 0;
}
