  width: 100%;
  height: 60%;
  box-sizing: border-box;
  margin: 20px;
  background-color: #292f3bbd;
  display: flex;
  flex-direction: row;
}

.projectPhoto {
  width: 60%;
}

.projectPhoto1 {
  background-image: url(/static/media/kanban.e4f4d56f.jpg);
  background-size: cover;
}

.projectPhoto2 {
  background-image: url(/static/media/scorekeeper.401da041.jpg);
  background-size: cover;
}

.projectPhoto3 {
  background-image: url(/static/media/kanban.e4f4d56f.jpg);
  background-size: cover;
}

.projectPhoto4 {
  background-image: url(/static/media/scorekeeper.401da041.jpg);
  background-size: cover;
}

.projectPhoto5 {
  background-image: url(/static/media/kanban.e4f4d56f.jpg);
  background-size: cover;
}

.projectPhoto6 {
  background-image: url(/static/media/scorekeeper.401da041.jpg);
  background-size: cover;
}

.projectDescribe {
  width: 40%;
  margin: 0;
  padding: 30px;
  color: #a09090f8;
}

.links {
  width: 80%;
  height: 50px;
  display: flex;
  flex-direction: row;
  margin-top: 60px;
}

.links>div {
  width: 300px;
  height: 35px;
  background-color: #2c323fc5;
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.9s;
}

.githublink>a, .demolink>a {
  text-decoration: none;
  color: #ff5d55e7;
}

.links>div:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  background-color: #363c4dde;
  color: #fd6961e7;
  box-shadow: -10px 10px 10px #242933;
}




.loader {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

div[class^="circle_"] {
    position: absolute;
    top: 50px;
    left: 50px;
    border-radius: 50%;
    transition: 1s;
    -webkit-animation: loading 1.5s linear 1;
            animation: loading 1.5s linear 1;
}

.circle_1 {
    background-color: #ffa09dcc;
    width: 150px;
    height: 150px;
}

.circle_2 {
    background-color: #ff8b85c5;
    width: 250px;
    height: 250px;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.circle_3 {
    background-color: #fe5e56b4;
    width: 350px;
    height: 350px;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

.circle_4 {
    background-color: #fe5e568c;
    width: 450px;
    height: 450px;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

.circle_5 {
    background-color: #fe5e5667;
    width: 550px;
    height: 550px;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

.outside_circle {
    width: 650px;
    height: 650px;
    background-color: #fe5f563f;
    position: relative;
    border-radius: 50%;
}

@-webkit-keyframes loading {
    0% {opacity: 0};
    10% {opacity: 0.1};
    20% {opacity: 0.2};
    30% {opacity: 0.3};
    40% {opacity: 0.4};
    50% {opacity: 0.5};
    60% {opacity: 0.6};
    70% {opacity: 0.7};
    80% {opacity: 0.8};
    90% {opacity: 0.9};
    100% {opacity: 1};
}

@keyframes loading {
    0% {opacity: 0};
    10% {opacity: 0.1};
    20% {opacity: 0.2};
    30% {opacity: 0.3};
    40% {opacity: 0.4};
    50% {opacity: 0.5};
    60% {opacity: 0.6};
    70% {opacity: 0.7};
    80% {opacity: 0.8};
    90% {opacity: 0.9};
    100% {opacity: 1};
}
.contact {
  margin: 0;
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.contact__header {
  margin-bottom: 50px;
  font-size: 50px;
  letter-spacing: 2.5px;
  color: #94a9bd;
}

.contact__links {
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.contact__official, .contact__social, .contact__other {
  display: flex;
  flex-direction: column;
  font-size: 20px;
}

.contact__official>p {
  margin: 30px 0 0;
  color: #81a0bd;
  transition: .8s;
}

.contact__official>a, .contact__other>a, .contact__social>a {
  margin-top: 30px;
  display: flex;
  align-items: center;
  color: #81a0bd;
  text-decoration: none;
  transition: .8s;
}

.contact__official>a:hover, .contact__other>a:hover, .contact__social>a:hover {
  color: #e48480;  
}

.contact__official>p:hover {
  color: #e48480;  
}

.contact__official>h2, .contact__other>h2, .contact__social>h2 {
  text-align: center;
  letter-spacing: 2px;
  color: #e48480;
} 

.contact__icon {
  margin-right: 20px;
  font-size: 40px;
  color:#81a0bd;
}

.contact__official_transition-appear {
  opacity: 0;
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}

.contact__official_transition-appear-active {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: all 0.6s ease-in-out;
}

.contact__social_transition-appear {
  opacity: 0;
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
}

.contact__social_transition-appear.contact__social_transition-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: all 0.6s ease-in-out;
}

.contact__other_transition-appear {
  opacity: 0;
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}

.contact__other_transition-appear.contact__other_transition-appear-active {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: all 0.6s ease-in-out;
}
